.img-fluid{
    max-width:100%;
    height:auto;
}
.mb-20{
    margin-bottom:20px;
}
.bg-wrapper{
    margin: -60px 30px 0px;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    z-index: 3;
    position: relative;
    background: #FFFFFF;
    padding:40px 0;
}
.container {
    max-width: 1200px;
    margin: auto;
}
h3.header-three {
    text-align: center;
    font-size: 38px;
}
.project-wrapper{
    position:relative;
}
.project-wrapper:after{
    content: '';
    position:absolute;
    background-color:rgba(0,0,0,0.4);
    width:100%;
    height:100%;
    left:0;
    top:0;
    z-index:0;
}
.project-title {
    font-size: 36px;
    display: block;
    color: #00acc1;
    position: absolute;
    top: 50%;
    right: 25px;
    margin: auto;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 30px;
    z-index:1;
}