.clear {
  clear: both;
}
.mx-15 {
  margin: 0px 15px;
}
.bg-wrapper {
  margin: -60px 30px 0px;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
    0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  z-index: 3;
  position: relative;
  background: #ffffff;
  padding: 40px 0;
}
.container {
  max-width: 1200px;
  margin: auto;
}
.img-fluid {
  max-width: 100%;
  display: block;
  margin: auto;
}
.btn-blue {
  /*background-color: #00acc1; */
  color: #00acc1;
  font-weight: bold;
  text-align: center;
  padding: 6px 8px;
   box-shadow: 1px 2px 3px #00acc1;
  border-radius: 3px;
  cursor: pointer;
  margin-bottom: 15px;
  border: 1px solid #00acc1;
}
.btn-default {
  width: 100px;
  background-color: #fff;
  color: #000;
  font-weight: bold;
  text-align: center;
  padding: 6px 8px;
  box-shadow: 1px 2px 3px #000;
  border-radius: 3px;
  cursor: pointer;
}
.btn-secondary {
  width: 100px;
  display: inline-block;
  background-color: #00acc1;
  color: #fff;
  font-weight: bold;
  text-align: center;
  padding: 6px 8px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  border-radius: 3px;
  cursor: pointer;
}
.active{
     background-color: #00acc1;
    color: #fff;
    box-shadow: 1px 2px 3px #000;
}
.research-outer {
  position: relative;
  box-shadow: 2px 2px 5px #ddd;
  margin-bottom: 30px;
  height: 350px;
  background-color: rgba(246, 249, 252, 1);
}
.research-detail {
  padding: 10px 12px 14px 12px;
  background-color: rgba(246, 249, 252, 1);
}
.research-title {
  font-size: 18px;
  font-weight: 600;
}
.research-type {
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(0, 172, 193, 0.6);
  color: #fff;
  padding: 4px 6px;
}
.height {
  height: 45px;
}
.research-session {
  font-size: 12px;
  margin-bottom: 15px;
}
span.research-author {
  font-weight: 500;
}
.research-date {
  font-weight: bold;
  font-size: 14px;
}
.research-pp {
  float: right;
}
.filter-outer {
  margin-bottom: 25px;
  text-align: center;
}
