.bg-wrapper{
    margin: -60px 30px 0px;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    z-index: 3;
    position: relative;
    background: #FFFFFF;
    padding:40px 0;
}
.container {
  max-width: 1200px;
    margin: auto;
}
h3.header-three {
    text-align: center;
    color: #3C4858;
    margin: 1.75rem 0 0.875rem;
    margin-top: 30px;
    min-height: 32px;
    font-weight: 700;
    font-family: "Microsoft Yahei", "Helvetica", sans-serif;
    margin-bottom: 1rem;
    text-decoration: none;
}
.btn-primary {
    max-width: 100px;
    display: block;
    background-color: #fff;
    color: #000;
    font-weight:bold;
    text-align: center;
    padding: 6px 8px;
    box-shadow: 0 2px 1px rgba(255,255,255,0.7);
    border-radius: 3px;

}
.btn-primary:hover{
    background-color: #000;
    color: #fff;
    font-weight:bold;
    box-shadow: 0px 0px 0px #000;
}
.timeline-winner {
    margin-bottom: 10px;
}
.timeline-label{
    font-weight: bold;
}
.timeline-organizer, .timeline-list {
    margin-bottom: 8px;
}
/* The actual timeline (the vertical ruler) */
.timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
}

.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: #00acc1;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}

.timeline-wrapper {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
}

.timeline-wrapper::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: white;
    border: 4px solid #00acc1;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}

.left {
    left: 0;
}

.right {
    left: 50%;
}

.left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid white;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent white;
}

.right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
}

.right::after {
    left: -16px;
}


.content {
    padding: 20px 30px;
    background: linear-gradient(60deg, #30c9dc,#00acc1);
    color:#fff;
    position: relative;
    border-radius: 6px;
}

.modal{
    border-radius: 6px
}

.modalHeader{
    border-bottom: none;
    padding: 24px 24px 0;
    min-height: 16.43px
}

.modalTitle{
    margin: 0;
    line-height: 1.42857143
}

.modalCloseButton{
    color: #999999;
    margin-top: -12px;
    padding: 0;
    cursor: pointer;
    background: 0 0;
    border: 0;
    font-size: inherit;
    opacity: .9;
    text-shadow: none;
    font-weight: 700;
    line-height: 1;
    float: right
}

.modalClose{
    width: 16px;
    height: 16px
}

.modalBody{
    padding: 24px 24px 16px;
    position: relative
}

.modalFooter{
    text-align: right;
    padding: 0 15px 15px;
    margin: 0
}

.modalFooterCenter{
    margin-left: auto;
    margin-right: auto
}

.modalImg{
    max-width: 100%;
}

@media screen and (max-width: 600px) {
    .timeline::after {
        left: 32px;
    }

    .timeline-wrapper::after {
        left: 31px;
    }

    .timeline-wrapper {
        width: 100%;
        padding-left: 70px;
        padding-right: 25px;
    }

    .timeline-wrapper::before {
        left: 60px;
        border: medium solid white;
        border-width: 10px 10px 10px 0;
        border-color: transparent white transparent transparent;
    }

    .left::after, .right::after {
        left: 15px;
    }
    .right {
        left: 0%;
    }
}
